'use strict'

const test = require('ava') //para test unitarios

let config = {
  logging: function(){}
}
let db = null

test.beforeEach(async() => {
  const setupDatabase = require('../')
  db = await setupDatabase(config)
})

test('Agent', t => {
  t.truthy(db.Agent, 'Agente existe')  //truthy que exita el valor que no sea 0
})
